import { Pagination } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/pagination';

import bgImg from "../../images/background/skill-background.jpg";
// 
import "../../css/mediaQuery.css"

export const Skills = () => {

    const skillStyle = {
        backgroundImage: `url(${bgImg})`
      }
      const swiperStyle ={
        height: "50vh",
        width: "80vw",
        backgroundColor: "white",
        borderRadius: "8px",
        padding: "8px 16px",
        position: "absolute",
        top: "50%",
        left: "50%",
        transform: "translate(-50%, -50%)",
        textAlign: "center",
      }
      const titleStyle = {
        margin: "0",
        fontFamily: "'Vujahday Script', curive",
        fontSize: "4rem"
      }

    return(
        <section id="skills" style={skillStyle}>
            <div style={swiperStyle}>
                <h2 className="skillTitle" style={titleStyle}>Skills</h2>
                <Swiper
                    modules={ [Pagination] }
                    pagination={{ clickable: true }}
                    slidesPerView={1}
                    style={{height: "75%"}}
                >
                    <SwiperSlide className="slide">
                        <div className="skillWrapper">
                            <h3>HTML,CSS</h3>
                            <div style={{ color: "#FFCC00" }}>
                                <i className="fas fa-star"></i>
                                <i className="fas fa-star"></i>
                                <i className="fas fa-star"></i>
                                <i className="fas fa-star"></i>
                                <i className="far fa-star"></i>   
                            </div>    
                        </div>
                        <p className="skillText">
                            HTML
                            CSS
                        </p>
                    </SwiperSlide>  

                    <SwiperSlide className="slide">
                        <div className="skillWrapper">
                           <h3>JS,React,TypeScript</h3> 
                           <div style={{ color: "#FFCC00" }}>
                                <i className="fas fa-star"></i>
                                <i className="fas fa-star"></i>
                                <i className="fas fa-star"></i>
                                <i className="far fa-star"></i>
                                <i className="far fa-star"></i>    
                            </div>
                        </div>
                        <p className="skillText">
                            JavaScript
                            React
                            TypeScript
                            Firebase
                        </p>
                    </SwiperSlide>  

                    <SwiperSlide className="slide">
                        <div className="skillWrapper">
                            <h3>Photoshop,Figma</h3>
                            <div style={{ color: "#FFCC00" }}>
                                <i className="fas fa-star"></i>
                                <i className="fas fa-star"></i>
                                <i className="far fa-star"></i>
                                <i className="far fa-star"></i>
                                <i className="far fa-star"></i>    
                            </div>  
                        </div>
                        <p className="skillText">
                            
                            PhotoshopFigma
                        </p>
                    </SwiperSlide>  
                </Swiper>
            </div>
        </section>
    )
}
